import React from "react"
import { Helmet } from "react-helmet"

import Navbar from "../components/navbar"
import Footer from "../components/footer"
import "../styles/global.scss"

export default ({ children }) => (
  <React.Fragment>
    <Helmet>
      <meta charSet="utf-8" />
      <title>Hannah Nguyen</title>
    </Helmet>
    <Navbar />
    {children}
    <Footer />
  </React.Fragment>
)
